import { Formik } from 'formik';
import { clientAPI } from '../../service/axios.config';
// import * as Yup from 'yup';
// import { useFormik } from 'formik';
import DayOfWeek from 'Components/DayOfWeek/DayOfWeek';
// import IMask from 'imask';
// import InputMask from 'react-input-mask';
import Input from 'react-phone-number-input/input';
// import 'react-phone-number-input/style.css';
// import PhoneInput from 'react-phone-number-input';
import { ToastContainer } from 'react-toastify';
import {
  Wrapper,
  Dialog,
  KindStyle,
  SubBTN,
} from './SignUp.styled';
import { useEffect, useState } from 'react';

const SignUp = ({ Close, kind_trainee, day, time, date }) => {
  const [dayTranslate, setDayTranslate] = useState('');
  const [tel, setTel] = useState('');

  useEffect(() => {
    const DayOfWeekTranslate = () => {
      if (day === "monday") {
        return (setDayTranslate(''));
      };
      if (day === "tuesday") {
        return (setDayTranslate(''));
      };
      if (day === "wednesday") {
        return (setDayTranslate(''));
      };
      if (day === "thursday") {
        return (setDayTranslate(''));
      };
      if (day === "friday") {
        return (setDayTranslate(`'`));
      };
      if (day === "saturday") {
        return (setDayTranslate(''));
      };
      if (day === "sunday") {
        return (setDayTranslate(''));
      };
      return (null);
    };
    DayOfWeekTranslate();
  }, [day]);

  const CheckKindTrainee = () => {
    if (kind_trainee === "-") {
      return (
        <>
          <Dialog>
            <h3>,           .  ,   </h3>
            <button type="button" onClick={Close}>
              
            </button>
          </Dialog>
        </>
      )
    };
    return (
      <>
        <Dialog>
          <h4>  <KindStyle>{kind_trainee}  <DayOfWeek day={day} time={time} /></KindStyle></h4><br />
          <h4> ,   </h4><br />
          <Formik
            initialValues={{
              id: tel,
              day_translate: dayTranslate,
              info: {
                date: date,
                day: day,
                time: time,
                kind_trainee: kind_trainee,
                name: "",
              }
            }}
            onSubmit={async values => {
              await clientAPI.sendDataUsers(values);
              // await new Promise(resolve => setTimeout(resolve, 500));
              // alert(JSON.stringify(values, null, 2));
            }}
          >
            {props => {
              const {
                values,
                isSubmitting,
                handleChange,
                handleBlur,
                handleSubmit,
              } = props;
              return (
                <form onSubmit={handleSubmit}>
                  <label htmlFor="info.name">
                     '
                  </label><br />
                  <input
                    autoFocus
                    id="name"
                    onChange={handleChange}
                    value={values.name}
                    maxLength='16'
                    onBlur={handleBlur}
                  /><br />
                  <label
                  // htmlFor="id"
                  >
                      
                  </label><br />
                  <Input
                    // id="id"
                    // onChange='setTel(); handleChange()'
                    // autoFocus="autofocus"
                    autoFocus

                    maxLength='16'
                    value={tel}
                    onBlur={handleBlur}
                    // value={values.value}
                    onChange={setTel}
                    // defaultCountry="UA"
                    country="UA"
                    international
                    withCountryCallingCode
                  /><br />
                  <SubBTN type="button" onClick={Close}>
                    
                  </SubBTN>
                  <SubBTN type="submit" disabled={isSubmitting}>
                    
                  </SubBTN>
                </form>
              );
            }}
          </Formik>
          <div>
            <ToastContainer
              style={{ marginTop: '55px', marginLeft: '25px', width: '250px' }}
              position="top-left"
              autoClose={false}
              newestOnTop={false}
              closeOnClick
              rtl={false}
              pauseOnFocusLoss
              draggable
              theme="dark"
            />
          </div>
        </Dialog>
      </>)
  };

  return (
    <Wrapper>
      <CheckKindTrainee />
    </Wrapper>
  )
};

export default SignUp;